import { Component, OnInit } from '@angular/core'; import { Router, ActivatedRoute } from '@angular/router'; import { Subscription } from 'rxjs/Subscription';  import { Grupo_auditoria } from '../../../modelo/Grupo_auditoria'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual'; import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'grupo_auditoria-dados-details',   templateUrl: 'grupo_auditoria_dadosDetails.component.html' }) export class Grupo_auditoriaDadosDetailsComponent implements OnInit {    grupo_auditoria: Grupo_auditoria;    private sub: Subscription;    private errorMessage: string;    constructor(private route: ActivatedRoute, private pesquisaService: PesquisaService) { }    ngOnInit() {       this.sub = this.route.parent.params.subscribe(params => {         let o : ObjetoVirtual = new ObjetoVirtual();         o.classeJava = "br.com.primum.modelo.grupo_auditoria.Grupo_auditoria";                 let id = +params['id'];         this.grupo_auditoria = new Grupo_auditoria();         this.grupo_auditoria.cd_grupo_auditoria = id;         o.objetoJson = this.grupo_auditoria;          this.pesquisaService.efetuarPesquisa(o)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);       });   }    onEfetuarPesquisa(result : any) {     if (!result) {       this.errorMessage = 'Houve um erro ao pesquisa a grupo_auditoria. '; }      this.grupo_auditoria = result;   }    ngOnDestroy() {     this.sub.unsubscribe();   } }


